.li {
  padding: 1.2rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.36);
  border-radius: 10px;
  background-color: white;
  max-width: 38rem;
  margin: 2rem auto;
  transition: all 0.2s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.li:hover {
  transform: translateY(-7px);
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.36);
}
