@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: "Varela Round", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #cfdae4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Card_card__TRdqI {
  background: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.36);
  border-radius: 10px;
  margin: 4rem auto;
  max-width: 40rem;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  border-radius: 10px;
}

form .NewItem_input__YlCyL {
  width: 20rem;
  height: 2rem;
  margin: 1rem;
  padding: 0.3rem 0.7rem;
  border-radius: 10px;
  border: none;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.16);
  transition: all 0.2s;
  font-family: "Varela Round", sans-serif;
}

form .NewItem_input__YlCyL:focus {
  outline: none;
}

form .NewItem_input__YlCyL:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.36);
}
button {
  width: 7rem;
  padding: 0.5rem;
  margin: 1rem;
  font-family: inherit;
  border-radius: 10px;
  border: none;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.36);
  background-color: #cfdae4;
  transition: all 0.2s;
}
button:hover {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.72);
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

button:active {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
}

.NewItem_notValid__2ZIPm {
  background-color: #666c72;
  color: white;
  -webkit-animation: NewItem_shake__4lqnN 0.5s 2;
          animation: NewItem_shake__4lqnN 0.5s 2;
}

@-webkit-keyframes NewItem_shake__4lqnN {
  0% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  25% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  50% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  75% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  100% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
}

@keyframes NewItem_shake__4lqnN {
  0% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  25% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  50% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  75% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  100% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
}

.TodoItem_li__1r1li {
  padding: 1.2rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.36);
  border-radius: 10px;
  background-color: white;
  max-width: 38rem;
  margin: 2rem auto;
  transition: all 0.2s;
  display: flex;
  justify-content: space-between;
  align-items: center;
