form {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  border-radius: 10px;
}

form .input {
  width: 20rem;
  height: 2rem;
  margin: 1rem;
  padding: 0.3rem 0.7rem;
  border-radius: 10px;
  border: none;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.16);
  transition: all 0.2s;
  font-family: "Varela Round", sans-serif;
}

form .input:focus {
  outline: none;
}

form .input:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.36);
}
button {
  width: 7rem;
  padding: 0.5rem;
  margin: 1rem;
  font-family: inherit;
  border-radius: 10px;
  border: none;
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.36);
  background-color: #cfdae4;
  transition: all 0.2s;
}
button:hover {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.72);
  transform: translateY(-5px);
}

button:active {
  transform: translateY(1px);
}

.notValid {
  background-color: #666c72;
  color: white;
  animation: shake 0.5s 2;
}

@keyframes shake {
  0% {
    transform: translateX(10px);
  }
  25% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(10px);
  }
  75% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(10px);
  }
}
